// app/chat/page.tsx (Server Component)

import { headers } from "next/headers";
import ChatPage from "@/component/chatService/ChatPage";

export default async function Page() {
  const headerList = await headers();

  console.log("=======================================");
  console.log("🔍 Next.js Request Headers (Server Log) 🔍");

  const allHeaders: { [key: string]: string | null } = {};
  headerList.forEach((value, key) => {
    allHeaders[key] = value;
  });
  console.log(allHeaders);
  console.log("=======================================");

  // ✅ WritePage처럼 localStorage에서 가져오는 방식이 아닌,
  // 서버 헤더에서 가져오거나 기본값을 설정
  let userSignId: string = headerList.get("userSignId") ||
      headerList.get("x-user-id") ||
      "";

  // ✅ userName도 헤더에서 시도
  let userName: string = headerList.get("x-user-name") ||
      headerList.get("userName") ||
      userSignId ||
      "사용자";

  try {
    userName = decodeURIComponent(userName);
  } catch (e) {
    // 디코딩 실패 시 원본 사용
  }

  // ⚠️ 서버 컴포넌트에서는 localStorage 접근 불가
  // 클라이언트에서 처리하도록 빈 값 전달
  return (
      <ChatPage
          currentUserId={userSignId}
          currentUserName={userName}
      />
  );
}